const Inventory = () => {
  const [productList, setProductList] = useState([]);
  const [reload, setReload] = useState(true);
  const [searchValue, setSearchValue] = useState("");
  const info = useSelector((state) => state.info);
  const store_uuid = info.store.uuid;
  const branch_uuid = info.branch.uuid;
  const store_setting = info.store.general_configuration? JSON.parse(info.store.general_configuration): setting


  const [openFilter, setOpenFilter] = React.useState(false);
  const dispatch = useDispatch();
  const handleToggleFilter = () => {
    setOpenFilter(!openFilter);
  };
  const importProductByJSON = async (jsonData) => {
    try {
      setOpenProductImportPopper(true);
      setIsLoadingProduct(true);
      const res = await storeApi.importProductJSON(store_uuid, branch_uuid, jsonData);
      if (res.status) {
        setIsLoadingProduct(false);
        setOpenProductImportPopper(false);
        setReload(!reload);
        dispatch(statusAction.successfulStatus("Nháº­p hÃ ng thÃ nh cÃ´ng"))
      } else {
        setIsLoadingProduct(false);
        setProductErrors(res.data);
        dispatch(statusAction.failedStatus("Nháº­p hÃ ng tháº¥t báº¡i"))
      }
      // setOpen(true);
    } catch (err) {
      console.log(err);
      dispatch(statusAction.failedStatus("Nháº­p hÃ ng tháº¥t báº¡i"))
      setIsLoadingProduct(false);
      // setOpen(false);
    }
    
  };

  useEffect(() => {
    setPagingState({ ...pagingState, page: 0 });
  }, [reload, store_uuid, branch_uuid]);

  useEffect(() => {
    setPagingState({ ...pagingState, page: 0 });
  }, []);

  const initialQuery = {
    orderBy: "products.created_at",
    sort: "desc",
    searchKey: "",
    minStandardPrice: "",
    maxStandardPrice: "",
    minListPrice: "",
    maxListPrice: "",
    minInventory: "",
    maxInventory: "",
    status: "",
    categoryId: "",
  };
  const handleRemoveFilter = () => {
    setQuery(initialQuery);
  };
  const [query, setQuery] = useState(initialQuery);
  const [pagingState, setPagingState] = useState({
    page: 0,
    limit: 10,
  });

  const [totalRows, setTotalRows] = useState(0);
  useEffect(() => {
    setPagingState({ ...pagingState, page: 0 });
  }, [reload, store_uuid, query]);

  useEffect(() => {
    const loadData = async () => {
      try {
        const response = await productApi.getProductsOfBranch(
          store_uuid,
          branch_uuid,
          {
            page: pagingState.page,
            limit: pagingState.limit,
            ...query,
          }
        );
        setTotalRows(response.total_rows);
        setProductList(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    if (store_uuid && branch_uuid) {
      loadData();
    }
  }, [pagingState.page, pagingState.limit, branch_uuid, reload, query]);


  const theme = useTheme();
  const classes = useStyles(theme);
  const xsScreen = useMediaQuery(theme.breakpoints.down("xs"));
  const [open, setOpen] = React.useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  //category
  const [openCategory, setOpenCategory] = React.useState(false);
  const handleClickOpenCategory = () => {
    setOpenCategory(true);
  };
  const handleCloseCategory = () => {
    setOpenCategory(false);
  };

  //status add
  const [addStatus, setAddStatus] = React.useState(null);

  //noti
  const [openBar, setOpenBar] = React.useState(false);
  const handleCloseBar = () => {
    setOpenBar(false);
  };

  //collapse
  const [openRow, setRowOpen] = React.useState(null);
  const handleOpenRow = (row) => {
    if (row !== openRow) {
      setRowOpen(row);
    } else {
      setRowOpen(null);
    }
  };

  // header sort
  const [order, setOrder] = React.useState("asc");
  const [orderBy, setOrderBy] = React.useState("id");

  const handleRequestSort = (event, property) => {
    //// (gá»­i order vs orderBy lÃªn api) -> fetch láº¡i data Ä‘á»ƒ sort
    // const isAsc = orderBy === property && order === 'asc';
    // setOrder(isAsc ? 'desc' : 'asc');
    // setOrderBy(property);
  };

  // toolbar
  const componentRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });

  /// import product by file
  const [openProductImportPopper, setOpenProductImportPopper] = useState(false);
  const [isLoadingProduct, setIsLoadingProduct] = useState(false);
  const [productErrors, setProductErrors] = useState([]);


  // const handleClickRecommend = async () => {
  //   if (store_uuid && branch_uuid) {
  //     try {
  //       const res = await productApi.productOrderRecommend(store_uuid, branch_uuid);
  //       alert(JSON.stringify(res.data, null, 2))
  //     } catch (err) {
  //       console.log(err);
  //       openNotification('error', 'KhÃ´ng thá»ƒ táº¡o gá»£i Ã½', '')
  //     }
  //   }
  // }

  const tableRef = React.createRef();
  return (
    <Card className={classes.root}>
      <ProductImportPopper
        open={openProductImportPopper}
        loading={isLoadingProduct}
        errors={productErrors}
        handleClose={() => {
          setOpenProductImportPopper(false)
          setProductErrors([])
        }}
      />
      <Grid container direction="row" justifyContent="space-between">
        {/* 1. ADD POP UP */}
        <Typography className={classes.headerTitle} variant="h5">
          Sáº£n pháº©m
        </Typography>
        <Grid className={classes.btngroup}>
          <Tooltip title="Xem danh má»¥c">
            <Button
              variant="outlined"
              color="primary"
              className={classes.button}
              onClick={handleClickOpenCategory}
            >
              Danh má»¥c
            </Button>
          </Tooltip>
          {/* <Button
              variant="outlined"
              color="primary"
              className={classes.button}
              onClick={handleClickRecommend}
            >
              Gá»£i Ã½ Ä‘áº·t hÃ ng
            </Button> */}

          <ButtonBase sx={{ borderRadius: "16px" }} onClick={handleClickOpen}>
            <Avatar variant="rounded" className={classes.headerAvatar}>
              <Tooltip title="ThÃªm sáº£n pháº©m">
                <AddIcon stroke={1.5} size="1.3rem" />
              </Tooltip>
            </Avatar>
          </ButtonBase>
        </Grid>
      </Grid>

      {/* Popup add */}
      <Category open={openCategory} handleClose={handleCloseCategory} />
      {open && (
        <AddInventory
          open={open}
          handleClose={handleClose}
          setReload={() => setReload(!reload)}
        />
      )}
      {/* Noti */}
      <SnackBar
        openBar={openBar}
        handleCloseBar={handleCloseBar}
        addStatus={addStatus}
      />

      <Divider />
       
      {/* 2. SEARCH - FILTER - EXPORT*/}
      {/* SAU NÃ€Y Sá»¬A Láº I TRUYá»€N DATA SAU KHI FILTER, SORT, Lá»ŒC Cá»˜T VÃ€O */}
      <ToolBar
        textSearch={"MÃ£ , TÃªn sáº£n pháº©m,..."}
        dataTable={productList}
        tableType={TableType.INVENTORY}
        handlePrint={handlePrint}
        handleSearchValueChange={setSearchValue}
        handleToggleFilter={handleToggleFilter}
        hasImport={true}
        importByJSON={importProductByJSON}
        excel_data={excel.excel_data_product}
        excel_name={excel.excel_name_product}
        customizable={true}
        customExcel = {CustomExcel}
        columnsToKeep={[
          { dbName: "product_code", displayName: "MÃ£ sáº£n pháº©m" },
          { dbName: "name", displayName: "Sáº£n pháº©m" },
          { dbName: "bar_code", displayName: "MÃ£ váº¡ch" },
          { dbName: "list_price", displayName: "GiÃ¡ bÃ¡n" },
          { dbName: "standard_price", displayName: "GiÃ¡ vá»‘n"},
          {dbName: "quantity_per_unit", displayName:"ÄÆ¡n vá»‹"},
          {dbName: "category_id",displayName:"Danh má»¥c"},
          // { dbName: "img_urls", displayName: "HÃ¬nh áº£nh (url1, url2,...)" },
          { dbName: "img_urls", displayName: "HÃ¬nh áº£nh" },

          { dbName: "quantity_available", displayName: "Tá»“n kho" },
          { dbName: "min_reorder_quantity", displayName: "Tá»“n nhá» nháº¥t" },
          {dbName:"max_order",displayName:"Tá»“n lá»›n nháº¥t"},
          {dbName:"has_batches",displayName:"LÃ´"},
          {dbName:"desciption",displayName:"MÃ´ táº£"}
        ]}
        orderByOptions={
          store_setting?.inventory.status ?
         [
          { value: "products.created_at", label: "NgÃ y táº¡o" },
          { value: "products.list_price", label: "Gia ban" },
          { value: "products.standard_price", label: "Gia von" },
          { value: "products.quantity_available", label: "Ton kho" },
        ]:
        [
          { value: "products.created_at", label: "NgÃ y táº¡o" },
          { value: "products.list_price", label: "Gia ban" },
          { value: "products.standard_price", label: "Gia von" },
        ]}
        orderBy={query.orderBy}
        setOrderBy={(value) => setQuery({ ...query, orderBy: value })}
        sort={query.sort}
        setSort={(value) => setQuery({ ...query, sort: value })}
        searchKey={query.searchKey}
        setSearchKey={(value) => setQuery({ ...query, searchKey: value })}
        handleRemoveFilter={handleRemoveFilter}
      />

      <InventoryFilter
        openFilter={openFilter}
        setQuery={setQuery}
        query={query}
        setProductList={setProductList}
        handleToggleFilter={handleToggleFilter}
        isManageInventory={store_setting?.inventory.status }
      />

      {/* 3. TABLE */}
      {!xsScreen ? (
        <TableWrapper
          pagingState={{ ...pagingState, total_rows: totalRows }}
          setPagingState={setPagingState}
          list={productList}
          tableRef={tableRef}
        >
          <TableHeader
            classes={classes}
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            headerData={store_setting?.inventory.status ?HeadCells.InventoryHeadCells:
              HeadCells.InventoryHeadCells.filter(item => item.id !== "inventory" &&  item.id !== "quantity")
            }
          />
          <TableBody>
            {productList.map((row, index) => {
              return (
                <InventoryTableRow
                  key={row.uuid}
                  row={row}
                  setReload={() => setReload(!reload)}
                  openRow={openRow}
                  handleOpenRow={handleOpenRow}
                  isManageInventory={store_setting?.inventory.status}
                />
              );
            })}

          </TableBody>
          

        </TableWrapper>
      ) : (
        <>
        <Box style={{minHeight:'50vh'}}>
          {productList.map((row, index) => {
            return (
              // <ProductMiniTableRow
              //   key={row.uuid}
              //   row={row}
              //   setReload={() => setReload(!reload)}
              //   openRow={openRow}
              //   handleOpenRow={handleOpenRow}
              // />
              // <BillMiniTableRow key={row.uuid} row={row} openRow={openRow} handleOpenRow={handleOpenRow}  onReload={() => setReload(!reload)}
              // totalCost={row.total_amount}  id={row.purchase_order_code} partnerName={row.supplier_name} date={row.creation_date}
              // typeBill={"Sáº£n pháº©m"} />
              <ProductMiniTableRow
                key={row.uuid}
                row={row}
                openRow={openRow}
                handleOpenRow={handleOpenRow}
                onReload={() => setReload(!reload)}
                img={JSON.parse(row.img_urls ? row.img_urls : '[]')?.at(0) || defaultProduct}
                id={row.product_code}
                name={row.name}
                list_price={row.list_price}
                standard_price={row.standard_price}
                branch_quantity={row.branch_quantity}
                min_reorder_quantity={row.min_reorder_quantity}
                typePartner={"Sáº£n pháº©m"}
                isManageInventory={store_setting?.inventory.status}

              />
            );
          })}
           </Box>
          <Pagination
            pagingState={{ ...pagingState, total_rows: totalRows }}
            setPagingState={setPagingState}
            list={productList}
            
          />
        </>
      )}

      <div style={{ display: "none" }}>
        <div ref={componentRef}>
          <ComponentToPrint productList={productList} classes={classes} query={query}/>
        </div>
      </div>
    </Card>
  );
};
export default Inventory;

const ComponentToPrint = ({ productList, classes, query }) => {
  const info = useSelector((state) => state.info);
  const store_setting = info.store.general_configuration? JSON.parse(info.store.general_configuration): setting

  return (

    <div style={{padding:10}}>
    <Typography style={{color:'#000'}}>NgÃ y láº­p:  {moment(new Date()).format("DD/MM/YYYY HH:mm")}</Typography>
    <Box style={{ margin: 10,flexGrow: 1,  textAlign: "center" ,color: "#000"}}>
      <Typography style={{  fontSize: 20, fontWeight: 500}} >
        Thá»‘ng kÃª sáº£n pháº©m
      </Typography>
      {query.searchKey ? <Typography  > {`TÃ¬m kiáº¿m theo: ${query.searchKey}`} </Typography>:null}
      {query.status? <Typography  > {`TÃ¬nh tráº¡ng: ${query.status}`} </Typography>:null}
      {/* {query.categoryId? <Typography  > {`Danh má»¥c: ${query.categoryId === "cash"?"TiÃªn máº·t":"Tháº»"}`} </Typography>:null} */}
      {query.minStandardPrice || query.maxStandardPrice ? <Typography  > {`GiÃ¡ nháº­p tá»«: ${query.minStandardPrice?query.minStandardPrice:0}Ä‘ Ä‘áº¿n ${query.maxStandardPrice?query.maxStandardPrice:0}Ä‘`} </Typography>:null}
      {query.minListPrice || query.maxListPrice ? <Typography  > {`GiÃ¡ bÃ¡n tá»«: ${query.minListPrice?query.minListPrice:0}Ä‘ Ä‘áº¿n ${query.maxListPrice?query.maxListPrice:0}Ä‘`} </Typography>:null}
      {query.minInventory || query.maxInventory ? <Typography  > {`Tá»“n kho tá»«: ${query.minInventory?query.minInventory:0} Ä‘áº¿n ${query.maxInventory?query.maxInventory:0}`} </Typography>:null}

    </Box>
    <div>
    <TableWrapper  isReport={true} >
      <TableHeader
      color="#000"
        classes={classes}
        headerData={HeadCells.InventoryHeadCells.filter(item => item.id !== "quantity")}
      />
      <TableBody>
        {productList.map((row, index) => {
          console.log("rowrow",row)
          return  <InventoryTableRow colorText={"#000"} key={row.uuid} row={row} hidenCollumn={["quantity", "image"]}isManageInventory={store_setting?.inventory.status} />
          })}
      </TableBody>
      </TableWrapper>
    </div>
  </div>
  );
};

